import React, {useState, useRef} from 'react';
import JoditEditor from 'jodit-react'

export const AddTodo = ({addTodo, setValue}) => {

    const editor = useRef(null);
    const [title, setTitle] = useState("");
    // const [desc, setDesc] = useState("");
    const submit = (e) =>{
        e.preventDefault();
        if(!title){
            alert("Title or Description cannot be blank.");
        }

        addTodo(title);
    }
  return (
    <div className='container mt-5'>
        <h2 className='text-center'>Add Todos</h2>
        <form onSubmit={submit}>
            <div className="mb-3">
                <label htmlFor="title" className="form-label">Title</label>
                <input type="text" value={title} onChange={(e)=>{setTitle(e.target.value)}} className="form-control" id="title" placeholder="Write the Title"/>
            </div>
            <div className="mb-3">
                <label htmlFor="desc" className="form-label">Description</label>
                <JoditEditor ref={editor} onChange={(content) => setValue(content)} /> 
            </div>
            
            <button type="submit" className="btn btn-danger">ADD</button>
        </form>
    </div>
  )

}
